<template>
  <div style="display: flex; justify-content: center;">
    <div style="display: flex; flex-direction: column;">
      <select v-model="selectedCategory" :disabled="started" class="custom-select">
        <option v-for="(category, index) in categories" :value="category" :key="index">
          {{ category }}
        </option>
      </select>
      <input type="text" v-model="detail" class="form-control mt-3" :disabled="started">
      <div class="mt-3">
        <button class="btn btn-start" @click="startTime" v-if="!started">START</button>
        <button class="btn btn-stop" @click="stopTime" v-if="started">STOP</button>
      </div>
      <p style="font-size: 3.5rem;">{{ formattedTime }}</p>
    </div>
  </div>
</template>
<script>
import moment from 'moment';
export default {
  data () {
    return {
      categories: [],
      selectedCategory: '',
      detail: '',
      created_date: '',
      time_started: '',
      time_finished: '',
      started: false,
      timerState: 'stopped',
      currentTimer: 0,
      formattedTime: "00:00:00",
      ticker: undefined,
      fullDetails: []
    }
  },
  mounted () {
    this.categories = JSON.parse(localStorage.getItem('Category')) ? JSON.parse(localStorage.getItem('Category')) : []
    this.fullDetails = JSON.parse(localStorage.getItem('Details')) ? JSON.parse(localStorage.getItem('Details')) : []
  },
  methods: {
    startTime () {
      this.started = true
      if (this.timerState !== 'running') {
        this.tick()
      }
      this.created_date = moment().format()
      this.time_started = moment().format('HH:mm:ss')
      this.timerState = 'running'
    },
    stopTime () {
      window.clearInterval(this.ticker)
      this.time_finished = moment(this.created_date).add(this.currentTimer, 'seconds').format('HH:mm:ss')
      let details = {
        category: this.selectedCategory,
        detail: this.detail,
        timeElapse: `${this.time_started} - ${this.time_finished}`,
        timeStarted: this.time_started,
        timeFinished: this.time_finished,
        totalSeconds: this.currentTimer,
        created_date: moment(this.created_date).format('YYYY-MM-DD'),
        current_month: moment(this.created_date).format('YYYY-MM')
      }

      this.fullDetails.push(details)

      localStorage.setItem('Details', JSON.stringify(this.fullDetails))

      this.clearDetails()
    },
    tick () {
      this.ticker = setInterval(() => {
        this.currentTimer++
        this.formattedTime = this.formatTime(this.currentTimer)
      }, 1000)
    },
    formatTime (seconds) {
      let measuredTime = new Date(null)
      measuredTime.setSeconds(seconds)
      let MHSTime = measuredTime.toISOString().substr(11, 8)
      return MHSTime
    },
    clearDetails () {
      this.selectedCategory = ''
      this.detail = ''
      this.currentTimer = ''
      this.timerState = 'stopped'
      this.started = false
      this.time_started = ''
      this.time_finished = ''
      this.created_date = ''
      this.formattedTime = "00:00:00"
    }
  }
}
</script>
<style scoped>
  .btn {
    margin: .375rem;
    color: inherit;
    text-transform: uppercase;
    white-space: normal;
    cursor: pointer;
    border: 0;
    border-radius: 10px;
    padding: .84rem 2.14rem;
    font-size: .81rem;
    outline: 0;
  }
  .btn-start {
    color: #fff !important;
    background-color: #00c851 !important;
  }
  .btn-stop {
    color: #fff;
    background-color: #ff3547 !important;
  }
</style>
